import type { PayloadAction } from '@reduxjs/toolkit';
import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';

const oordinatesInitialState = {
  lat: null as number | null, // lat: 51.5341714,
  lon: null as number | null, // lon: 33.3767724,
};

export type oordinatesInitialStateType = typeof oordinatesInitialState;

export const setUserCoordinatesTC = createAsyncThunk(
  'oordinates/setUserCoordinatesTC',
  (_, thunkAPI) => {
    navigator.geolocation.getCurrentPosition((position) => {
      thunkAPI.dispatch(
        setCoordinatesAC({
          lat: position.coords.latitude,
          lon: position.coords.longitude,
        }),
      );
    });
  },
);

export const oordinatesSlice = createSlice({
  name: 'oordinates',
  initialState: oordinatesInitialState,
  reducers: {
    setCoordinatesAC(
      state,
      action: PayloadAction<{ lat: number; lon: number }>,
    ) {
      state.lat = action.payload.lat;
      state.lon = action.payload.lon;
    },
  },
});

export const oordinatesReducer = oordinatesSlice.reducer;
export const { setCoordinatesAC } = oordinatesSlice.actions;
